<template>
  <div id="app">
    <router-view />
    <audio
      loop="loop"
      preload="preload"
      id="bgm"
      controls="controls"
      ref="bgm"
      style="display: none"
    >
      <source src="./assets/audio/bgm.mp3" />
    </audio>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import { getJsConfig } from "@/api/api";
export default {
  name: "app",
  computed: {
    ...mapState(["isPlaying"]),
  },
  mounted() {
    // this.$refs.bgm.play()
    // this.audioAutoPlay()
    // document.addEventListener('WeixinJSBridgeReady', this.wxready, false)
    this.init();
  },
  // destroyed() {
  // document.removeEventListener('WeixinJSBridgeReady', this.wxready)
  // },
  methods: {
    init() {
      getJsConfig({
        url: "",
      }).then((res) => {
        if (res.status === 200) {
          let config = res.data || {};
          jWeixin.config({
            debug: false,
            appId: config.appId,
            timestamp: config.timestamp,
            nonceStr: config.nonceStr,
            signature: config.signature,
            jsApiList: [],
          });
          jWeixin.ready(() => {
            let audio = document.getElementById("bgm");
            audio.play();

            jWeixin.hideMenuItems({
              menuList: [
                "menuItem:copyUrl",
                "menuItem:share:appMessage",
                "menuItem:share:timeline",
                "menuItem:share:qq",
                "menuItem:share:weiboApp",
                "menuItem:share:facebook",
                "menuItem:share:QZone",
              ],
            });

            jWeixin.hideOptionMenu();
          });
        } else {
          this.handleError(res);
        }
      });
    },
    // wxready() {
    //   let audio = document.getElementById('bgm')
    //   audio.play()

    //   WeixinJSBridge.call('hideOptionMenu');
    // },
    // audioAutoPlay() {
    //   let audio = document.getElementById('bgm')

    //   let play = () => {
    //     document.removeEventListener('WeixinJSBridgeReady', play)
    //     audio.play()
    //   }

    //   audio.play()

    //   document.addEventListener('WeixinJSBridgeReady', play, false)
    // }
  },
  watch: {
    isPlaying(b) {
      if (b) {
        this.$refs.bgm.play();
      } else {
        this.$refs.bgm.pause();
      }
    },
  },
};
</script>

<style>
html,
body,
#app {
  width: 100%;
  height: 100%;
  font-family: myFont;
}
* {
  touch-action: none !important;
  touch-action: manipulation !important;
  
}
</style>
