<template>
  <div class="playContainer" id="playContainer" @click="togglePlay">
    <img src="../../assets/music.png" />
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState(["isPlaying"]),
  },
  mounted() {
    document.querySelector('#playContainer').style.animationPlayState = 'paused'
    if (this.isPlaying) {
      document.querySelector('#playContainer').style.animationPlayState = ''
    }
  },
  methods: {
    ...mapMutations(["ChangePlayState"]),
    togglePlay() {
      this.ChangePlayState();
      if (this.isPlaying) {
        // this.rotate();
        document.querySelector('#playContainer').style.animationPlayState = ''
      } else {
        document.querySelector('#playContainer').style.animationPlayState = 'paused'
      }
    },
    // rotate() {
      // if (!this.isPlaying) return
      // console.log(123)
      // let angle = this.rotateAngle + 1
      // if (angle > 360) {
      //   angle = 0
      // }
      // console.log('angle', angle, this.rotateAngle)
      // this.$store.dispatch('changeAngle', angle)
    //   requestAnimationFrame(this.rotate)
    // }
  }
}
</script>

