import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    outTime: '',
    isPlaying: true, // 是否正在播放
    rotateAngle: 0, // 旋转角度
    jlnowTime: 0,
    // 校园分区
    schoolindex: ''
  },
  getters: {

  },
  mutations: {
    schoolindexChange(state, str) {
      state.schoolindex = str
    },
    TimeChange(state, str) {
      // console.log(str);
      this.state.outTime = str
    },
    ChangePlayState(state) {
      state.isPlaying = !state.isPlaying
    },
    ChangeAngle(state, num) {
      console.log('change', num)
      state.rotateAngle = num
    },
    jlnowTimeChange(state, str) {
      // console.log(state, str);
      state.jlnowTime = str
    }
  },
  actions: {
    changeAngle(ctx, num) {
      ctx.commit('ChangeAngle', num)
    }
  },
  modules: {
  },
  plugins: [createPersistedState({
    storage: window.localStorage,
    reducer(val) {
      // console.log(val);
      return {
        // 只储存state中的assessmentData
        dqsj: val.dqsj,
        jlnowTime: val.jlnowTime,
        schoolindex: val.schoolindex

      }
    }
  })]
})
